import "./Title.css";
import { React, useEffect, useState } from "react";
import { throttle } from "lodash";
import $ from "jquery";
import classnames from 'classnames';

function Title () {
  return (

    <header className="header">
      <div className="inner">
        <div className="title">
          <div className="title_1">-  -</div>
          <div className="title_2">  </div>
          <div className="title_2-1"> </div>
          <div className="title_2-2"></div>
          <div className="title_box1"></div>
          <div className="title_3"></div>
          <div className="title_4">   .</div>
          <div className="title_box2" onClick={()=>{
            let location_Aboutme = $('#Aboutme').offset().top - 120
            window.scroll({top: location_Aboutme, behavior:'smooth'})
            }}>
            <div><span>  </span></div>
          </div>
        </div>  

    </div>
  </header>

  )
}

export default Title;
